<template>
  <Accordion v-model:activeIndex="currentIndex">
    <AccordionPanel :value="0">
      <div id="experience">
        <AccordionHeader class="main-header centered-header">
          Experience&nbsp;
        </AccordionHeader>
        <AccordionContent>
          <employmentHistory />
        </AccordionContent>
      </div>
    </AccordionPanel>

    <AccordionPanel :value="1">
      <div id="vocationalAchievements">
        <AccordionHeader class="main-header centered-header">
          Vocational Achievements&nbsp;
        </AccordionHeader>
        <AccordionContent>
          <vocationalAchievements />
        </AccordionContent>
      </div>
    </AccordionPanel>

    <AccordionPanel :value="2">
      <div id="skills">
        <AccordionHeader class="main-header centered-header">
          Skills&nbsp;
        </AccordionHeader>
        <AccordionContent>
          <skills />
        </AccordionContent>
      </div>
    </AccordionPanel>

    <AccordionPanel :value="3">
      <div id="education">
        <AccordionHeader class="main-header centered-header">
          Education&nbsp;
        </AccordionHeader>
        <AccordionContent>
          <education />
        </AccordionContent>
      </div>
    </AccordionPanel>

    <AccordionPanel :value="4">
      <div id="volunteeringExperience">
        <AccordionHeader class="main-header centered-header">
          Volunteering Experience&nbsp;
        </AccordionHeader>
        <AccordionContent>
          <volunteeringExperience />
        </AccordionContent>
      </div>
    </AccordionPanel>

    <AccordionPanel :value="5">
      <div id="hobbies">
        <AccordionHeader class="main-header centered-header">
          Hobbies&nbsp;
        </AccordionHeader>
        <AccordionContent>
          <hobbies />
        </AccordionContent>
      </div>
    </AccordionPanel>
  </Accordion>
</template>

<script setup>
import { computed } from 'vue';
import Accordion from 'primevue/accordion';
import AccordionPanel from 'primevue/accordionpanel';
import AccordionHeader from 'primevue/accordionheader';
import AccordionContent from 'primevue/accordioncontent';
import employmentHistory from './employmentHistory.vue';
import vocationalAchievements from './vocationalAchievements.vue';
import skills from './skills.vue';
import education from './education.vue';
import volunteeringExperience from './volunteeringExperience';
import hobbies from './hobbies.vue';

const props = defineProps({
  activeIndex: Number
});

const emit = defineEmits(["update:activeIndex"]);

const currentIndex = computed({
  get() {
    return props.activeIndex;
  },
  set(newValue) {
    console.log(`Accordion activeIndex changed to: ${newValue}`);
    emit("update:activeIndex", newValue);
  }
});
</script>

<style scoped>
body {
  background-color: initial;
}

.centered-header {
  display: flex;
  justify-content: center;
  width: 100%;
  text-align: center;
}

.main-header {
  font-size: 1.8rem;
}
</style>