<template>
  <Dock :model="menuItems" position="right">
    <template #itemicon="{ item }">
      <button
        style="cursor: pointer; font-weight: bold; font-size: 6px; border: none; background: transparent;"
        @click.stop="item.command()"
      >
        {{ item.label }}
      </button>
    </template>
  </Dock>
</template>

<script setup>
import { ref, inject } from 'vue';
import Dock from 'primevue/dock';
import { onMounted } from 'vue';
import { nextTick } from 'vue';

onMounted(() => {
  document.querySelectorAll('.p-dock-item-link').forEach((el) => {
    el.removeAttribute('aria-hidden'); // Prevent blocking focus
  });
});

const activeIndex = inject('activeIndex'); // Get activeIndex from parent
const setActiveIndex = inject('setActiveIndex'); // Function to update activeIndex

const debugClick = () => console.log("Dock item clicked!"); // Debug event listener

const scrollToSection = (sectionId, panelIndex) => {
  console.log(`Expanding panel: ${panelIndex}, Target section ID:`, sectionId);
  console.log(`Type of sectionId:`, typeof sectionId);

  setActiveIndex(panelIndex);

  nextTick(() => {
    setTimeout(() => {
      const section = document.getElementById(sectionId);

      if (section) {
        console.log(`Scrolling to: ${sectionId}`);
        section.scrollIntoView({ behavior: 'smooth' });
      } else {
        console.warn(`Section not found: ${sectionId}`);
      }
    }, 200);
  });
};


const menuItems = ref([
  { label: "Experience", index: 0, command: () => scrollToSection("experience", 0) },
  { label: "Vocational Achievements", index: 1, command: () => scrollToSection("vocationalAchievements", 1) },
  { label: "Skills", index: 2, command: () => scrollToSection("skills", 2) },
  { label: "Education", index: 3, command: () => scrollToSection("education", 3) },
  { label: "Volunteering", index: 4, command: () => scrollToSection("volunteeringExperience", 4) },
  { label: "Hobbies", index: 5, command: () => scrollToSection("hobbies", 5) }
]);
</script>

<style scoped>
/* Ensure Dock is positioned correctly */
.p-dock {
  position: fixed;
  right: 10px;
  bottom: 20px;
  transform: scale(2);
}
.p-dock-item-link {
  display: block; /* Forces text to break onto new lines */
}

@media (max-width: 768px) {
  .p-dock {
    transform: scale(1); /* Adjust scale factor as needed for mobile */
    right: 5px;         /* Optionally, adjust positioning */
    bottom: 10px;
  }
}
</style>